@use 'colors';

@use 'fonts';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }


html{
    font-size: 62.5%;
    scroll-behavior: smooth;
}

a, a:visited , a:link{
    color: #eee;
    text-decoration: none;
}


body{
  font-size: 2rem;
  background: colors.$body;
  font-family: fonts.$font;
}
  

  